.app {
  padding: 20px;

  h1 {
    text-align: center;
  }

  .block {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #000;
    border-radius: 20px;

    h2 {
      text-align: center;
    }

    .btn {
      margin-top: 10px;
    }

    .info {
      margin-top: 10px;
      font-size: 18px;
      span {
        &:nth-child(1) {
          color: grey;
          font-style: italic;
        }
        &:nth-child(2) {
          color: green;
          font-weight: bold;
        }
      }
    }
  }

  .error {
    margin-top: 10px;
    color: red;
  }
}